export default {
    name: "lucky-code",
    components: {},
    data() {
        return {
            lottery_id: '',
            award: {user:{userInfo:{}}},
            num: 0,
            own: {user:{userInfo:{}}},
            page: 1,
            parent: [],
        }
    },

    onLoad(options) { this.$commonLoad.onload(options);
        const self = this;
        self.lottery_id = options.lottery_id;
        self.$showLoading({title: '加载中'});
        self.$request({
            url: self.$api.lottery.code,
            data: {
                lottery_id: self.lottery_id,
            }
        }).then(info => {
            self.$hideLoading();
            if (info.code === 0) {
                [self.award, self.num, self.own, self.parent] = [info.data.award, info.data.num, info.data.own, info.data.parent];
            }
        }).catch(e => {
            self.$hideLoading();
        })
    },
    methods: {
        more: function () {
            const self = this;
            if (self.args || self.load) return
            self.load = true;

            let page = self.page + 1;
            self.$request({
                url: self.$api.lottery.code,
                data: {
                    page: page,
                    lottery_id: self.lottery_id,
                }
            }).then(info => {
                if (info.code === 0) {
                    [self.page, self.args, self.parent] = [page, info.data.parent.length === 0, self.parent.concat(info.data.parent)];
                }
                self.load = false;
            })
        }
    }
}
